
.body{
  position: absolute;
}
.header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex: 1 1;
      flex: 1 1;
  height: 7%;
  background:#F0F0F0;
  border:1px solid #CCC;
  width:100%;
  
  z-index: 1;
}

.center-column-wrapper{
  position:relative;
  margin: auto;
  width: 85%;
  max-width: 800px;
  padding: 10px;
}

.header-buttons {
  -ms-flex-item-align: center;
      align-self: center;
  margin-top: auto;
}

.title {
  margin-top: auto;
  -ms-flex-item-align: start;
      align-self: flex-start;
  font-size: 1.75em;
}

.searchbar-wrapper {
  margin-top: 5vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.searchbar-dropdown {
  -webkit-transform: translateY(-4px);
      -ms-transform: translateY(-4px);
          transform: translateY(-4px);
}
.searchbar{
  max-width: 120px;
}

.linechart {
  max-width: 700px;
  max-height: 700px;

}

svg .line-chart__axis-tick,
svg .line-chart__axis-line {
  stroke: darkgray;
}
svg .line-chart__axis-text,
svg .line-chart__axis-text {
  font-family: sans-serif;
  font-size: 11px;
}
svg .line-chart__area-plot {
  stroke: steelblue;
  stroke-width: 2px;
}

.linechart p {
  text-align: center;
}

.card-control-wrapper {
  margin-left: 24%;
}



